<template>
  <ul class="nav-right" ref="navRight">
    <n-basket></n-basket>
    <n-user :iname="'مهرداد'"></n-user>
    <li v-if="rtl">
      <a id='sidebar-toggle' class="sidebar-toggle" href="javascript:void(0)">
        <i class="ti-menu"></i>
      </a>
    </li>
  </ul>
</template>
<script>
import { mapGetters } from 'vuex'
import * as $ from 'jquery'
// components
import Basket from './NavRight/Basket'
import User from './NavRight/User'
export default {
  name: 'NavRight',
  components: {
    'n-basket': Basket,
    'n-user': User
  },
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'isRtl'
    })
  },
  data () {
    return {
    }
  },
  mounted () {
    let that = this
    this.$nextTick(function () {
      $(this.$refs.navRight).find('.sidebar-toggle').on('click', e => {
        if (that.rtl) $('.app').toggleClass('is-collapsed-rtl')
        else $('.app').toggleClass('is-collapsed')

        e.preventDefault()
      })
    })
  }
}
</script>
<style>

</style>



